@import "variables";
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-weight: 100;
  color: $color-3;
  h1,
  h2 {
    font-family: $font-1;
  }
  font-family: $font-2;
}

body {
  background: $white;
}
li {
  list-style-type: none;
}
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
